<template>
    <div class="sliderModel">
        <div class="cont">
            <div id="canvas_wrap"></div>
            <div class="showMessage"></div>
        </div>
    </div>
</template>

<script>
import { slider } from "../util/newSlider";
import { useI18n } from "../i18nPlugin";
export default {
    mounted() {},
    setup() {
        const i18n = useI18n();
        return {
            i18n
        };
    },
    methods: {
        // 外部调用open显示
        open() {
            var that = this;
            function showMessage(msg, color) {
                var showMessage = document.querySelector(".showMessage");
                showMessage.innerHTML = msg;
                showMessage.style.color = color;
                if (color == "green") {
                    setTimeout(() => {
                        that.close();
                        that.$emit("loadingBox");
                    }, 1000);
                }
            }
            var obj = {
                el: "#canvas_wrap",
                w: 280,
                h: 150,
                imgArr: [
                    require("../assets/01bg.png"),
                    require("../assets/02bg.png"),
                    require("../assets/05bg.png"),
                    require("../assets/bottomArrow.png")
                ],
                sliderIcon: "images/sliderz/sangangy.png",
                refresh: function() {
                    showMessage("", "#333");
                },
                finish: function(e) {
                    e
                        ? showMessage(
                              that.i18n.$t("SliderCheck").success,
                              "green"
                          )
                        : showMessage(that.i18n.$t("SliderCheck").fail, "red");
                }
            };
            document.querySelector(".sliderModel").style.display = "block";
            new slider(obj);
        },
        // 外部调用close关闭
        close() {
            document.querySelector(".sliderModel").style.display = "none";
        }
    }
};
</script>
<style lang="scss" scoped>
* {
    margin: 0;
    padding: 0;
}
.cont {
    position: relative;
    top: 40%;
    transform: translate(0%, -50%);
}
.sliderModel {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;
}
.sliderModel .cont {
    padding: 20px;
    width: 280px;
    background: #fff;
    border-radius: 6px;
    margin: 50px auto;
}
.showMessage {
    text-align: center;
    font-size: 14px;
    height: 30px;
    line-height: 30px;
}

#canvas_wrap {
    width: 280px;
}
#canvas_wrap canvas {
    display: block;
}
</style>