<template>
    <div class="title1" anima="fadeInLeft">
        <div>
            <span class="green">{{i18n.$t('home').contact}}</span>
        </div>
        <div class="center">
            <!-- <img src="../../../assets/telephone.png" />
            <span class="sp">{{i18n.$t('home').telephone}} +86 133xxxxxxxx</span>-->
            <img src="../../../assets/email.png" />
            <span>{{i18n.$t('home').email}} service@qbaytek.com</span>
        </div>
        <div class="center">
            <img src="../../../assets/localtion.png" />
            <span>{{i18n.$t('home').local}} {{i18n.$t('home').localdetail}}</span>
        </div>
    </div>
    <div class="body1" anima="fadeInLeft">
        <el-row v-if="mobile">
            <el-col :span="8" :offset="2">
                <div class="item">
                    <span class="formtitle">{{i18n.$t('home').company}}</span>
                    <el-input :placeholder="i18n.$t('home').companytag" v-model="company" clearable></el-input>
                </div>
                <div style="display:flex" class="item">
                    <div class="box">
                        <span class="formtitle">{{i18n.$t('home').name}}</span>
                        <el-input :placeholder="i18n.$t('home').nametag" v-model="name" clearable></el-input>
                    </div>
                    <div>
                        <span class="formtitle">{{i18n.$t('home').telephone}}</span>
                        <el-input
                            :placeholder="i18n.$t('home').telephonetag"
                            v-model="telephone"
                            clearable
                        ></el-input>
                    </div>
                </div>
                <div class="item">
                    <span class="formtitle">{{i18n.$t('home').email}}</span>
                    <el-input :placeholder="i18n.$t('home').emailtag" v-model="email" clearable></el-input>
                </div>
                <div class="item">
                    <span class="formtitle">{{i18n.$t('home').detail}}</span>
                    <el-input
                        type="textarea"
                        :rows="8"
                        :placeholder="i18n.$t('home').detailtag"
                        v-model="detail"
                        resize="none"
                    ></el-input>
                </div>
                <div class="submit">
                    <el-button round @click="submit" class="loading">{{i18n.$t('home').submit}}</el-button>
                </div>
            </el-col>
            <el-col :span="10" :offset="2" class="bg">
                <!-- <div class="bg">1</div> -->
            </el-col>
        </el-row>
        <div v-else>
            <div class="item">
                <span class="formtitle">{{i18n.$t('home').company}}</span>
                <el-input :placeholder="i18n.$t('home').companytag" v-model="company" clearable></el-input>
            </div>
            <div style="display:flex" class="item">
                <div class="box">
                    <span class="formtitle">{{i18n.$t('home').name}}</span>
                    <el-input :placeholder="i18n.$t('home').nametag" v-model="name" clearable></el-input>
                </div>
                <div>
                    <span class="formtitle">{{i18n.$t('home').telephone}}</span>
                    <el-input
                        :placeholder="i18n.$t('home').telephonetag"
                        v-model="telephone"
                        clearable
                    ></el-input>
                </div>
            </div>
            <div class="item">
                <span class="formtitle">{{i18n.$t('home').email}}</span>
                <el-input :placeholder="i18n.$t('home').emailtag" v-model="email" clearable></el-input>
            </div>
            <div class="item">
                <span class="formtitle">{{i18n.$t('home').detail}}</span>
                <el-input
                    type="textarea"
                    :rows="8"
                    :placeholder="i18n.$t('home').detailtag"
                    v-model="detail"
                    resize="none"
                ></el-input>
            </div>
            <div class="submit">
                <el-button round @click="submit()" class="loading">{{i18n.$t('home').submit}}</el-button>
            </div>
        </div>
    </div>
    <SliderCheck @loadingBox="loadingBox" ref="SliderCheckA"></SliderCheck>
</template>

<script>
import { anima } from "../../../util";
import { useI18n } from "../../../i18nPlugin";
import { loading } from "../../../util/loading.js";
import { ElMessage } from "element-plus";
import { Api } from "@/api";
import SliderCheck from "../../../components/SliderCheck";
export default {
    name: "Home",
    setup() {
        const i18n = useI18n();
        return {
            i18n
        };
    },
    data() {
        return {
            company: "",
            name: "",
            telephone: "",
            email: "",
            detail: "",
            mobile: !this.$store.state.viewListen,
            verification: false
        };
    },
    components: { SliderCheck },
    mounted() {
        anima();
    },
    methods: {
        switchLanguage(params) {
            console.log(params);
            window.localStorage.setItem("language", params);
            this.i18n.locale.value = params;
        },
        goReception() {
            this.router.push("/reception");
        },
        loadingBox() {
            this.toggleBody(0);
            let obj = {
                company: this.company,
                name: this.name,
                telephone: this.telephone,
                email: this.email,
                detail: this.detail
            };
            if (!this.email)
                return ElMessage.error(this.i18n.$t("home").emailtag);
            if (!this.detail)
                return ElMessage.error(this.i18n.$t("home").detailtag);
            if (!this.telephone)
                return ElMessage.error(this.i18n.$t("home").telephonetag);
            if (!this.name)
                return ElMessage.error(this.i18n.$t("home").nametag);
            if (!this.company)
                return ElMessage.error(this.i18n.$t("home").companytag);
            let Rloading = loading(".loading");
            Api.submit_consultation(obj)
                .then(res => {
                    Rloading.close();
                    console.log(res);
                    if (res.data.code == 400) {
                        console.log(res.data.message);
                        ElMessage.error(this.i18n.$t("home").message);
                        return;
                    }
                    ElMessage.success({
                        message: this.i18n.$t("common").ok,
                        type: "success"
                    });
                    console.log("已成功提交");
                })
                .catch(err => {
                    if (
                        err.response.data.message ==
                        '/email should match format "email"'
                    ) {
                        ElMessage.error(this.i18n.$t("rules").email);
                    }
                    Rloading.close();
                });
        },
        submit() {
            document.documentElement.scrollTop = 0;
            this.$refs.SliderCheckA.open();
            this.toggleBody(1);
        },
        // 解决图片验证时，滚动条跟着乱动的问题
        toggleBody(isPin) {
            if (isPin) {
                document.body.style.height = "100vh";
                if (this.$store.state.viewListen) {
                    document.body.addEventListener("touchmove", this.handler, {
                        passive: false
                    });
                }
                document.body.style["overflow-y"] = "hidden";
                document.body.style["overflow-x"] = "hidden";
            } else {
                if (this.$store.state.viewListen) {
                    document.body.removeEventListener(
                        "touchmove",
                        this.handler,
                        {
                            passive: false
                        }
                    );
                }
                document.body.style.height = "unset";
                document.body.style["overflow-x"] = "auto";
                document.body.style["overflow-y"] = "auto";
            }
        },
        //禁止默认行为
        handler(ev) {
            ev.preventDefault();
        }
    }
};
</script>
<style lang="scss">
.fadeInLeft {
    -webkit-animation-name: fadeInLeft;
    animation-name: fadeInLeft;
    -webkit-animation-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
    animation-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
@keyframes fadeInLeft {
    0% {
        -webkit-transform: translate3d(-400px, 0, 0);
        transform: translate3d(-400px, 0, 0);
        opacity: 0;
    }
    100% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
}
.fadeInRight {
    -webkit-animation-name: fadeInRight;
    animation-name: fadeInRight;
    -webkit-animation-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
    animation-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
}
@keyframes fadeInRight {
    0% {
        -webkit-transform: translate3d(400px, 0, 0);
        transform: translate3d(400px, 0, 0);
        opacity: 0;
    }
    100% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
}
.title1 {
    width: 100%;
    min-height: 250px;
    background-color: #d7fbe8;
    text-align: center;
    font-weight: bold;
    .green {
        color: #1fab89;
        font-size: 30px;
        line-height: 100px;
    }
    .center {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 15px 0px;
        .sp {
            display: inline-block;
            margin-right: 40px;
        }
    }
    img {
        width: 20px;
    }
}
.body1 {
    padding-bottom: 50px;
    .el-input__inner {
        background-color: #f0f0f0;
        border: none;
    }
    .el-textarea__inner {
        background-color: #f0f0f0;
        border: none;
    }
    .item {
        .formtitle {
            font-weight: bold;
            color: #1fab89;
            margin: 10px 0px;
            display: inline-block;
        }
    }
    .submit {
        display: flex;
        flex-direction: row-reverse;
        margin-top: 10px;
        .el-button {
            width: 100px;
            background-color: #1fab89;
            color: #ffffff;
            font-weight: bold;
        }
    }
    .bg {
        background: url("../../../assets/reception.png") no-repeat center;
        background-size: cover;
    }
    .box {
        margin: 0 10px 0 0;
    }
}
@media all and (max-width: 600px) {
    .body1 {
        margin-bottom: 150px;
        padding: 0 20px;
        .submit {
            justify-content: center;
            .el-button {
                width: 200px;
            }
        }
        .item {
            flex-wrap: wrap;
            .box {
                width: 100%;
                margin: 0px;
            }
            div {
                width: 100%;
            }
        }
    }
}
</style>
