import { ElLoading } from 'element-plus';
export function loading(element) {
    const loading = ElLoading.service({ // 声明一个loading对象
        lock: true, // 是否锁屏
        target: element, // 需要遮罩的区域
        background: 'rgba(255, 255, 255, 0.6)',
        body: true,
    })
    return loading;
}