import $axios from './http'

// 获取商圈详情
export function get_business_center(params) {
  const url = 'get_business_center?id=' + params.id
  return $axios.get(url, params)
}
// 注册验证
export function register_account(params) {
  const url = 'register_account/v1'
  return $axios.post(url, params)
}
// 密码修改
export function reset_password(params) {
  const url = 'reset_password/v1'
  return $axios.post(url, params)
}
// 表单提交数据
export function submit_consultation(params) {
  const url = 'submit_consultation/v1'
  return $axios.post(url, params)
}